/* Twitter content script styling */

/* Profile token button */
.wumbo-token-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 9999px;
  padding: 0 16px;
  height: 32px;
  margin-left: 12px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s;
  border: none;
  outline: none;
  background-color: #8c52ff;
  color: white;
  font-size: 14px;
}

.wumbo-token-button:hover {
  background-color: #7a41e6;
}

.wumbo-icon {
  margin-right: 8px;
  font-size: 16px;
}

/* Tweet token button */
.wumbo-tweet-button-container {
  display: flex;
  align-items: center;
  margin-left: 8px;
}

.wumbo-tweet-token-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 9999px;
  width: 32px;
  height: 32px;
  padding: 0;
  cursor: pointer;
  transition: background-color 0.2s;
  border: none;
  outline: none;
  background-color: transparent;
  color: rgb(83, 100, 113);
}

.wumbo-tweet-token-button:hover {
  background-color: rgba(140, 82, 255, 0.1);
  color: #8c52ff;
}

.wumbo-tweet-token-button .wumbo-icon {
  margin-right: 0;
}

