  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  border: none;
  transition: all 0.2s;
}

.wumbo-token-buy-button {
  background-color: rgb(0, 186, 124);
  color: white;
}

.wumbo-token-buy-button:hover {
  background-color: rgb(0, 170, 114);
}

.wumbo-token-sell-button {
  background-color: rgb(244, 63, 94);
  color: white;
}

.wumbo-token-sell-button:hover {
  background-color: rgb(224, 43, 74);
}

/* Buy/sell modal */
.wumbo-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(15, 23, 42, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
}

.wumbo-modal {
  background-color: white;
  border-radius: 16px;
  width: 400px;
  max-width: 90%;
  overflow: hidden;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 8px 10px -6px rgba(0, 0, 0, 0.1);
}

.wumbo-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid #e2e8f0;
}

.wumbo-modal-header h2 {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
}

.wumbo-modal-close {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #64748b;
}

.wumbo-modal-body {
  padding: 20px;
}

.wumbo-input-group {
  margin-bottom: 16px;
}

.wumbo-input-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
  font-size: 14px;
}

.wumbo-input {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #cbd5e1;
  border-radius: 8px;
  font-size: 16px;
}

.wumbo-price-estimate {
  background-color: #f8fafc;
  padding: 12px;
  border-radius: 8px;
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
}

.wumbo-estimate-value {
  font-weight: bold;
}

