  padding: 12px;
  margin-bottom: 16px;
  border: 1px solid rgba(140, 82, 255, 0.2);
}

.tokenize-context-badge {
  display: inline-block;
  background-color: #8c52ff;
  color: white;
  font-size: 12px;
  font-weight: bold;
  padding: 4px 8px;
  border-radius: 4px;
  margin-bottom: 8px;
}

.tokenize-context-description {
  margin: 0;
  color: #475569;
}

