    color: #f8fafc;
  }
  
  .wumbo-price-estimate {
    background-color: #0f172a;
    color: #f8fafc;
  }
}

/* Enhanced price chart styling */
.wumbo-price-chart-container {
  position: relative;
  height: 200px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ddd;
}

.wumbo-price-bar {
