  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 10px;
  color: #777;
}

.wumbo-time-labels {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -20px;
  display: flex;
  justify-content: space-between;
  font-size: 10px;
  color: #777;
}

.wumbo-price-info {
  text-align: center;
  margin-top: 10px;
  font-weight: bold;
  color: #333;
}

