  display: block;
  margin-bottom: 5px;
  font-size: 14px;
  color: #555;
}

.wumbo-button-group {
  display: flex;
  gap: 5px;
}

.wumbo-percentage-button {
  flex: 1;
  padding: 8px 0;
  background-color: #f5f8fa;
  border: 1px solid #e1e8ed;
  border-radius: 4px;
  font-size: 14px;
  color: #1da1f2;
  cursor: pointer;
  transition: all 0.2s;
}

.wumbo-percentage-button:hover {
  background-color: #e8f5fe;
  border-color: #1da1f2;
}

.wumbo-percentage-button:active {
  transform: scale(0.98);
}

