  border-radius: 8px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: all 0.2s;
}

.wumbo-token-option:hover {
  background-color: #f5f8fa;
  border-color: #1da1f2;
}

.wumbo-token-name {
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 5px;
}

.wumbo-token-price,
